import React from 'react';
import { Grid as MuiGrid, Paper, Typography, Chip, withStyles, ExpansionPanel as EP, ExpansionPanelSummary as EPS, ExpansionPanelDetails as EPD } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import HoSData from './HoSData';
import Selector from './Selector';

const Grid = withStyles({item: {padding: 2}})(MuiGrid);
const ExpansionPanel = withStyles({expanded: {margin: 0}})(EP);
const ExpansionPanelSummary = withStyles({root: {padding: 0, minHeight: '28px !important'}, content: {margin: '0 !important'}, expandIcon: {padding: 0}})(EPS);
const ExpansionPanelDetails = withStyles({root: {padding: 0}})(EPD);

export default function Hero(props) {
	const {onChange} = props;
	const hero = JSON.parse(JSON.stringify(props.hero));
	const data = HoSData[props.slot][hero.hero];
	function change(type, key, value) {
		hero[type][key] = value;
		onChange(hero);
	}
	function calculateStat(stat) {
		var value = data.base[stat] || 0;
		for (var factor in data.factor[stat]) {
			value += data.factor[stat][factor] * (hero.att[factor] || hero[factor]);
		}
		return value;
	}
	const calcVal = HoSData.calcVal.bind(null, hero);
	function remain(type) {
		let total = 0;
		for (let i = 0; i <= hero.Level; i++) total += data.levels[type][i];
		for (let key in data.base[type]) total -= hero[type][key] - data.base[type][key];
		return total;
	}
	const ratt = remain('att');
	const rskl = remain('skl');
	const rtal = remain('tal');
	let maxSkill = 16;
	if (hero.Level >= 30) maxSkill = 18;
	if (hero.Level >= 40) maxSkill = 20;
	if (hero.Level >= 50) maxSkill = 22;
	if (hero.Level >= 60) maxSkill = 25;
	return (
		<Grid container spacing={0} style={{width:'100%',margin:0}}>
			<Grid item xs={4}>
				<Paper className="green">
					<Typography variant="h6">{hero.hero}</Typography>
				</Paper>
			</Grid>
			{Object.keys(data.factor).map(stat => 
				<Grid item xs={4} key={stat}>
					<Paper className="blue pill">
						<strong>
							<Chip className="badge" label={calculateStat(stat)}/>
							{stat}
						</strong>
					</Paper>
				</Grid>
			)}
			<Grid item xs={12}>
				<ExpansionPanel>
					<ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
						<Typography>Attributes <small>({ratt} remain)</small></Typography>
					</ExpansionPanelSummary>
					<ExpansionPanelDetails>
						<Grid container>
							{Object.keys(hero.att).map(attribute => 
								<Grid item xs={4} key={attribute}>
									<Selector title={attribute} value={hero.att[attribute]} min={data.base.att[attribute]} max={16} remain={ratt} onChange={v => change('att', attribute, v)}/>
								</Grid>
							)}
						</Grid>
					</ExpansionPanelDetails>
				</ExpansionPanel>
				<ExpansionPanel>
					<ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
						<Typography>Skills <small>({rskl} remain)</small></Typography>
					</ExpansionPanelSummary>
					<ExpansionPanelDetails>
						<Grid container>
							{Object.keys(hero.skl).map(skill => 
								<Grid item xs={4} key={skill}>
									<Selector title={skill} value={hero.skl[skill]} min={data.base.skl[skill]} max={maxSkill} remain={rskl} onChange={v => change('skl', skill, v)}/>
								</Grid>
							)}
						</Grid>
					</ExpansionPanelDetails>
				</ExpansionPanel>
				<ExpansionPanel defaultExpanded={true}>
					<ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
						<Typography>Talents <small>({rtal} remain)</small></Typography>
					</ExpansionPanelSummary>
					<ExpansionPanelDetails>
						<Grid container>
							<Grid item xs={12}>
								{Object.keys(hero.tal).map(talent => 
									<Grid container spacing={0} alignItems="center" style={{opacity: hero.tal[talent] ? 1 : 0.3}} key={talent}>
										<Grid item xs={4}>
											<Selector title={talent} value={hero.tal[talent]} min={data.base.tal[talent]} max={hero.Level >= data.talents[talent].level ? 10 : 0} remain={rtal} onChange={v => change('tal', talent, v)}/>
											<div style={{textAlign:'center',fontSize:12}}>{data.talents[talent].type}</div>
										</Grid>
										<Grid item xs={8}>
											<Grid container>
												{['cost', 'attack', 'heal', 'security', 'self', 'buff', 'curse'].map(type => data.talents[talent][type] &&
													<Grid item xs={type === 'cost' ? 3 : true} key={type}>
														<Paper className={`${type} card`}>
															<Typography>
																{Object.keys(data.talents[talent][type]).map(key => {
																	const val = calcVal(talent, type, key);
																	if (val === 0 && hero.tal[talent] > 0) return;
																	return <span key={key}>{key}:&nbsp;<span className="value">{val}</span> </span>;
																})}
															</Typography>
														</Paper>
													</Grid>
												)}
												{data.talents[talent].info && 
													<Grid item xs={12}>
														<Paper className={`info card`}>
															<Typography>
																{Object.keys(data.talents[talent].info).map(key => {
																	const val = calcVal(talent, 'info', key);
																	if (val === 0 && hero.tal[talent] > 0) return;
																	return <span key={key}>{key}: <span className="value">{val}</span> </span>;
																})}
															</Typography>
														</Paper>
													</Grid>
												}
											</Grid>
										</Grid>
									</Grid>
								)}
							</Grid>
						</Grid>
					</ExpansionPanelDetails>
				</ExpansionPanel>
			</Grid>
		</Grid>
	);
}