import React, { Component } from 'react';
import MediaQuery from 'react-responsive';
import { Tabs, Tab, AppBar } from '@material-ui/core';
import Hero from './Hero';
import Team from './Team';
import HoSData from './HoSData';
import pako from 'pako';
import './App.css';

class App extends Component {
	constructor(props) {
		super(props);
		if (window.location.hash) {
			let hash = window.location.hash.replace(/^#/,'');
			if (hash.length > 100)
				this.state = JSON.parse(pako.inflate(atob(hash), {to: 'string'}));
			else
				this.state = this.decodeState(hash);
			console.log(this.state);
		} else {
			this.state = {
				heroes: {
					"Warrior": { hero: "Kincaid" },
					"Mystic": { hero: "Kjartan" },
					"Healer": { hero: "Fyona" },
					"Sneak": { hero: "Selen" }
				},
				level: 17
			};

			for (let slot in this.state.heroes) this.resetHero(slot, this.state.heroes[slot]);
		}
	}

	resetHero(slot, hero) {
		const data = HoSData[slot][hero.hero];
		hero.slot = slot;
		hero.Level = this.state.level;
		hero.att = {};
		for (let a in data.base.att)
			hero.att[a] = data.base.att[a];
		hero.skl = {};
		for (let a in data.base.skl)
			hero.skl[a] = data.base.skl[a];
		hero.tal = {};
		for (let a in data.base.tal)
			hero.tal[a] = data.base.tal[a];
	}

	onChangeSlot(slot, hero) {
		this.setState(state => {
			const heroes = Object.assign({}, state.heroes);
			heroes[slot].hero = hero;
			this.resetHero(slot, heroes[slot]);
			return {heroes};
		}, this.updateHash.bind(this));
	}

	onChangeHero(slot, hero) {
		this.setState(state => {
			const heroes = Object.assign({}, state.heroes);
			heroes[slot] = hero;
			return {heroes};
		}, this.updateHash.bind(this));
	}

	onChangeLevel(level) {
		this.setState(state => {
			const heroes = Object.assign({}, state.heroes);
			for (let slot in heroes) {
				heroes[slot].Level = level;
			}
			return {level, heroes};
		}, this.updateHash.bind(this));
	}

	updateHash() {
		console.log(this.state);
		window.location.hash = this.encodeState();
	}

	encodeState() {
		let encoded = "";
		for (let slot in this.state.heroes) encoded += this.state.heroes[slot].hero[0];
		encoded += this.state.level;
		for (let slot in this.state.heroes) {
			encoded += '|';
			let hero = this.state.heroes[slot];
			let data = HoSData[slot][hero.hero];
			for (let att in data.base.att) encoded += hero.att[att].toString(36);
			for (let skl in data.base.skl) encoded += hero.skl[skl].toString(36);
			for (let tal in data.base.tal) encoded += hero.tal[tal].toString(36);
		}
		console.log(encoded);
		return encoded;
	}

	decodeState(encoded) {
		let state = {
			heroes: {
				Warrior: { hero: encoded[0] === "K" ? "Kincaid" : "Vraes", att: {}, skl: {}, tal: {} },
				Mystic: { hero: encoded[1] === "K" ? "Kjartan" : "Vincent", att: {}, skl: {}, tal: {} },
				Healer: { hero: encoded[2] === "F" ? "Fyona" : "Kyera", att: {}, skl: {}, tal: {} },
				Sneak: { hero: encoded[3] === "S" ? "Selen" : "Tamilin", att: {}, skl: {}, tal: {} }
			},
			level: parseInt(encoded.slice(4).split('|')[0], 10)
		};
		let parts = encoded.split('|');
		let heroes = {Warrior: parts[1], Mystic: parts[2], Healer: parts[3], Sneak: parts[4]};
		for (let slot in heroes) {
			let hero = state.heroes[slot];
			hero.slot = slot;
			hero.Level = state.level;
			let data = HoSData[slot][hero.hero];
			encoded = heroes[slot];
			let i = 0;
			for (let att in data.base.att) hero.att[att] = parseInt(encoded[i++], 36);
			for (let skl in data.base.skl) hero.skl[skl] = parseInt(encoded[i++], 36);
			for (let tal in data.base.tal) hero.tal[tal] = parseInt(encoded[i++], 36);
		}
		return state;
	}

	render() {
		const {heroes, level, tab = 'team'} = this.state;
		const slots = Object.keys(heroes);
		const onChangeLevel = this.onChangeLevel.bind(this);
		const onChangeSlot = this.onChangeSlot.bind(this);
		return (
			<div className="app-container">
				<MediaQuery query='(min-width:600px)'>
					<div className="team-column">
						<Team heroes={heroes} level={level} onTeamLevel={onChangeLevel} onSlot={onChangeSlot}/>
					</div>
					<div className="wide-container">
						<div className="wide-scroller">
							{slots.map(slot => (
								<div key={slot} className="hero-column">
									<Hero slot={slot} hero={heroes[slot]} onChange={hero => this.onChangeHero(slot, hero)}/>
								</div>
							))}
							<div style={{clear:'both'}}></div>
						</div>
					</div>
				</MediaQuery>
				<MediaQuery query='(max-width:599px)'>
					<AppBar>
						<Tabs variant="fullWidth" value={tab} onChange={(e, tab) => this.setState({tab})}>
							<Tab value='team' label='Team'/>
							{slots.map(slot => (
								<Tab key={slot} value={slot} label={heroes[slot].hero}/>
							))}
						</Tabs>
					</AppBar>
					<div style={{marginTop: 48}}>
						{tab === 'team' && <Team heroes={heroes} level={level} onTeamLevel={onChangeLevel} onSlot={onChangeSlot}/>}
						{tab !== 'team' && <Hero slot={tab} hero={heroes[tab]} onChange={hero => this.onChangeHero(tab, hero)}/>}
					</div>
				</MediaQuery>
			</div>
		);
	}
}

export default App;
